import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FestaPageComponent } from './containers/festa-page/festa-page.component';
import { FestaFormComponent } from './components/festa-form/festa-form.component';

export const routes: Routes = [
  {
    path: '',
    component: FestaPageComponent,
    data: { title: 'Festas' },
  },
  {
    path: 'criar-festa',
    component: FestaFormComponent,
    data: { title: 'Nova Festa'}
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FestaRoutingModule {}